  background-color: #1376f8;
  border: none;
  border-radius: 5px;
  width: 250px;
  padding: 10px;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  background-color: #0056b3;
}

button:focus {
  outline: none;
}

@media (width <= 500px) {
  .container {
